@use "styles/mixins" as *;

.cart_empty {
    padding: 4rem 0;
    @include flexBox;
    flex-direction: column;
    gap: 1rem;
    &__icon {
        $dimension: 12rem;
        width: $dimension;
        height: $dimension;
    }
    & > a > button {
        margin-top: 1rem;
    }
}
